.house.loaded {

  header.jumbo {
    height:340px;
    position:relative;
    background-size:cover;

    .jumbo-contents {
      position:absolute;
      left:50px;
      bottom:30px;
      line-height: 2.5em;
      
      * {
        color:#fff;
        margin:8px 0 0 0;  
        padding:0;
        text-shadow:0 0 6px rgba(0,0,0,.9);
        font-family: 'Slabo 27px', serif;
      }

      h1 {
        font-size:40px;
      }

      h2 {
        font-size:30px;
        font-weight:normal;
      }
      
      .house-properties {
        font-size:26px;
      }
    }
  }

  main { 
    
    .section {
      padding:50px 50px 0 50px;
      font-size:17px;
      font-family: 'Nunito Sans', serif;

      h3 {
        font-size:22px;
        color:#cb5526;
      }
    }

    .section.description {

      .mini-map {
        float:right;
        padding:4px 0 60px 90px;
  
        .leaflet-container {
          width:420px;
          height:240px;
          border-radius:3px;
          overflow:hidden;  
        }
      }
  
    }

    .section.images, .section.plans {

      .ant-image {
        margin-right:20px;
        border-radius:3px;
        overflow:hidden;
      }

    }

    .section.cite-as {
      
      p {
        background-color:#f0f4f9;
        padding:20px;
        border-radius:6px;
        font-family:'Times New Roman', Times, serif;
        font-size:18px;

        a {
          color:#545454;
          text-decoration:underline;
        }
      }

    }

    .section:last-child {
      padding-bottom:80px;
    }
  
  }

}